/* 위에는 이전 코드 */

#test-nav, #test-nav ul {
    list-style: none outside none;
    margin: 0;
    padding: 0;
}
#test-nav {
    display:flex;
    flex-direction: row;
    list-style-type: none;
    justify-content: space-between;
    clear: both;
    font-size: 12px;
    height: 58px;
    padding: 0 0 0 0;
    position: relative;
    width:100%;
    z-index: 9999;
}
#test-nav li {
    position: relative;
    width:100%;
    background-color: #FFFFFF;
}
#test-nav li:hover {
    background: none repeat scroll 0 0 rgb(71, 67, 67);
    outline: 0 none;
}
#test-nav li:hover > a {
    color: #FFFFFF;
}

#test-nav li a {
    top:25%;
    text-align: center;
    color: #121212;
    font-size: 20px;
    display: block;
    font-weight: bold;
    position: relative;
    text-decoration: none;
    font-family: 'Roboto', sans-serif;
}

#test-nav ul {
    background-color: #222;
    border-radius: 0 0 5px 5px;
    border-width: 0 1px 1px;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.5);
    left: -9999px;
    overflow: hidden;
    position: absolute;
    top: -9999px;
    z-index: 2;
    -webkit-transform: scaleY(0);
    transform: scaleY(0);
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    transition: -webkit-transform 0.1s linear;
    transition: transform 0.1s linear;
    transition: transform 0.1s linear, -webkit-transform 0.1s linear;
}

#test-nav li:hover ul {
    left: 0px;
    top: 58px;
    width: 100%;
    -webkit-transform: scaleY(1);
    transform: scaleY(1);
}

#test-nav ul li {
    background: none;
    width: 100%;
}
#test-nav ul li a {
    float: none;
    font-size: 15px;
    color:#FFFFFF;
    padding: 5px 0 5px 0;
}
#test-nav ul li:hover {
    background-color: #121212;
    color: #00B4FF;
}
