/*이미지 슬라이드*/
.banner_wrap {
    text-align: center;
    height:600px;
    margin-bottom: 20px;
    border-radius: 30px;
    box-shadow: 0 2px 5px rgb(0, 0, 0, 0.1);
}

.slide_banner {
    position: relative;
    width: 100%;
    height: 100%;
}

.slide_banner:hover .pre_arrow,
.slide_banner:hover .next_arrow {
    cursor: pointer;
    opacity: 1;
    transition: 0.7s;
}

.banner_image {
    width: 100%;
    height: 100%;
    border-radius: 15px;
    position: absolute;
        background-repeat: no-repeat;
        background-size: cover;
    left: 0;
    opacity: 0;
    transition: opacity 0.8s ease-in-out;
}

.banner_image.active {
    opacity: 1;
}

.pre_arrow,
.next_arrow {
    position: absolute;
    top: 250px;
    width: 70px;
    height: 100px;
    background-color: rgb(255, 255, 255, 0.2);
    display: flex;
    border-radius: 5px;
    opacity: 0;
}

.pre_arrow img,
.next_arrow img {
    width: 70px;
}

.pre_arrow {
    left: 0;
}

.next_arrow {
    right: 0;
}
